<template>
	<div>
		<ul class="collapse-list collapse-list-result m-0">
			<li id="mappingRuleTitle" class="collapse-item">
				<button
					type="button"
					class="collapse-link"
					data-toggle="collapse"
					data-target="#mappingRule"
					ref="mappingRuleTitleBtn"
					@click="mappingTitleBtnClick"
				>
					<span>{{ $t('label.mapping.rule') }}</span>
				</button>
				<div class="collapse show" id="mappingRule">
					<div class="card card-body">
						<div class="form-inline m-full" v-if="!this.data.readonly">
							<button type="button" class="btn-icon" @click="addRule" :title="$t('label.mapping.rule.add')">
								<i class="icon-plus-circle"></i>
							</button>
							<button type="button" class="btn-icon" @click="removeRule" :title="$t('label.mapping.rule.remove')">
								<i class="icon-minus-circle"></i>
							</button>
						</div>
						<div ref="mappingRuleGridParent" class="table-responsive">
							<TuiGrid ref="mappingRuleGrid" v-bind="mappingRuleOptions" @click="ruleClick($event.rowKey)" />
						</div>
					</div>
				</div>
			</li>
			<li class="collapse-item">
				<button type="button" class="collapse-link" data-toggle="collapse" data-target="#mappingInfo" @click="mappingTitleBtnClick">
					<span>{{ $t('label.model.info') }}</span>
				</button>
				<div class="collapse show" id="mappingInfo">
					<div class="card card-body">
						<div class="form-inline m-full" v-if="!this.data.readonly">
							<button type="button" class="btn-icon" @click="initMappingInfo" :title="$t('label.initialize')">
								<i class="icon-reset"></i>
							</button>
							<button type="button" class="btn-icon" @click="getSameIdInfo" :title="$t('label.same.id.link')">
								<i class="icon-deal"></i>
							</button>
							<button type="button" class="btn-icon" @click="removeSingleMapping" :title="$t('label.delete.single.mapping')">
								<i class="icon-minus-circle"></i>
							</button>
						</div>
						<div ref="mappingGridArea" :style="style">
							<div ref="sourceGridParent" class="table-responsive">
								<TuiGrid ref="sourceGrid" v-bind="sourceModelOptions" />
							</div>
							<div ref="mappingArrowParent" class="table-responsive" @wheel="mappingArrowParentWheel">
								<canvas ref="mappingArrow"></canvas>
							</div>
							<div ref="targetGridParent" class="table-responsive">
								<TuiGrid ref="targetGrid" v-bind="targetModelOptions" />
							</div>
						</div>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
import { WindowResizeEventBus, SideMenuToggleEventBus, makeGridOptions, escapeHtml, HttpReq } from '@/utils/common.js';
import { Grid } from '@toast-ui/vue-grid';
import cloneDeep from 'lodash/cloneDeep';

export default {
	props: {
		data: Object
	},
	name: 'MappingInfo',
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			dragger: null,
			mappingRuleGridSelectedRowKey: null,
			style: {
				height: null,
				'overflow-y': `hidden`
			},
			sourceScrollTop: 0,
			targetScrollTop: 0,
			mappingRuleOptions: {
				draggable: !this.data.readonly,
				columns: [
					{
						name: 'sourceRecordObject',
						hidden: true
					},
					{
						name: 'targetRecordObject',
						hidden: true
					},
					{
						name: 'pk.mappingId',
						hidden: true
					},
					{
						name: 'pk.mappingOrder',
						hidden: true
					},
					{
						name: 'ruleDetail',
						hidden: true
					},
					{
						name: 'pk',
						header: 'Mapping ID&Order',
						formatter: value => {
							return `${value.row['pk.mappingId']}$${value.row['pk.mappingOrder']}`;
						}
					},
					{
						name: 'targetForm',
						header: 'Target Form',
						editor: 'text'
					},
					{
						name: 'mappingDetailDesc',
						header: this.$t('label.desc'),
						editor: 'text'
					}
				],
				data: [],
				minBodyHeight: '55',
				bodyHeight: '120',
				onGridMounted: evt => {
					evt.instance.on('drop', () => {
						evt.instance.getData().forEach((info, idx) => {
							evt.instance.setValue(info.rowKey, 'pk.mappingOrder', idx);
						});
					});
				}
			},
			sourceModelOptions: {
				treeColumnOptions: {
					name: 'pk.fieldId'
				},
				scrollY: true,
				rowHeaders: [`rowNum`],
				columns: [
					{
						name: 'id',
						hidden: true
					},
					{
						name: 'fieldType',
						hidden: true
					},
					{
						name: 'pk.fieldId',
						header: 'Field',
						escapeHTML: true,
						formatter: ({ row }) => {
							let rtnFieldType = ``;

							if ('R' === row.fieldType) {
								if (row.recordObject) {
									rtnFieldType += `Group(${row.recordObject.recordType})`;
									if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
								}
							} else {
								const fieldTypeTranslateObj = {
									B: 'Byte',
									S: 'Short',
									I: 'Int',
									L: 'Long',
									F: 'Float',
									D: 'Double',
									T: 'String',
									N: 'Numeric',
									P: 'Decimal',
									A: 'Raw',
									p: 'Timestamp',
									b: 'Boolean',
									R: 'Record',
									v: 'Individual'
								};

								rtnFieldType += `${fieldTypeTranslateObj[row.fieldType]}(${row.fieldLength})`;

								if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
							}

							return escapeHtml('R' === row.fieldType ? row['pk.fieldId'] : `${row['pk.fieldId']}(${row.fieldName}) ${rtnFieldType}`);
						}
					}
				],
				data: [],
				onGridMounted: evt => {
					evt.instance.on('expand', () => {
						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on('collapse', evt => {
						evt.instance
							.getChildRows(evt.rowKey)
							.filter(row => `R` === row.fieldType)
							.forEach(row => {
								evt.instance.collapse(row.rowKey);
							});

						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on(`click`, evt => {
						if (`cell` !== evt.targetType) return;
						this.drawArrow();
					});

					evt.instance.gridEl.querySelector(`.tui-grid-rside-area .tui-grid-body-area`).addEventListener('scroll', evt => {
						this.sourceScrollTop = evt.target.scrollTop;

						setTimeout(() => {
							this.drawArrow();
						}, 0);
					});
				},
				onGridUpdated: () => {
					this.resize();
					this.initDragDropEvt();
				}
			},
			targetModelOptions: {
				treeColumnOptions: {
					name: 'pk.fieldId'
				},
				scrollY: true,
				rowHeaders: [`rowNum`],
				columns: [
					{
						name: 'id',
						hidden: true
					},
					{
						name: 'fieldType',
						hidden: true
					},
					{
						name: 'pk.fieldId',
						header: 'Field',
						escapeHTML: true,
						formatter: ({ row }) => {
							let rtnFieldType = ``;

							if ('R' === row.fieldType) {
								if (row.recordObject) {
									rtnFieldType += `Group(${row.recordObject.recordType})`;
									if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
								}
							} else {
								const fieldTypeTranslateObj = {
									B: 'Byte',
									S: 'Short',
									I: 'Int',
									L: 'Long',
									F: 'Float',
									D: 'Double',
									T: 'String',
									N: 'Numeric',
									P: 'Decimal',
									A: 'Raw',
									p: 'Timestamp',
									b: 'Boolean',
									R: 'Record',
									v: 'Individual'
								};

								rtnFieldType += `${fieldTypeTranslateObj[row.fieldType]}(${row.fieldLength})`;
								if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
							}

							return 'R' === row.fieldType ? row['pk.fieldId'] : `${row['pk.fieldId']}(${row.fieldName}) ${rtnFieldType}`;
						}
					}
				],
				data: [],
				onGridMounted: evt => {
					evt.instance.on('expand', () => {
						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on('collapse', evt => {
						evt.instance
							.getChildRows(evt.rowKey)
							.filter(row => `R` === row.fieldType)
							.forEach(row => {
								evt.instance.collapse(row.rowKey);
							});

						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on(`click`, evt => {
						if (`cell` !== evt.targetType) return;
						this.drawArrow();
					});

					evt.instance.gridEl.querySelector(`.tui-grid-rside-area .tui-grid-body-area`).addEventListener('scroll', evt => {
						this.targetScrollTop = evt.target.scrollTop;

						setTimeout(() => {
							this.drawArrow();
						}, 0);
					});
				},
				onGridUpdated: () => {
					this.resize();
					this.initDragDropEvt();
				}
			}
		};
	},
	created() {
		this.mappingRuleOptions = makeGridOptions(this.mappingRuleOptions);
		this.sourceModelOptions = makeGridOptions(this.sourceModelOptions);
		this.targetModelOptions = makeGridOptions(this.targetModelOptions);

		WindowResizeEventBus.$on('MainResize', this.mainResize);
		SideMenuToggleEventBus.$on('MainResize', this.mainResize);
	},
	mounted() {
		if (this.data.mappingObject) {
			this.data.mappingObject.mappingDetails.forEach(mappingDetail => {
				const sourceRecordObject = `I` === this.data.type ? this.data.interfaceRecordInfo : this.data.serviceRecordInfo;
				let targetRecordObject = `I` === this.data.type ? this.data.serviceRecordInfo[0] : this.data.interfaceRecordInfo[0];

				if (`I` === this.data.type) {
					targetRecordObject = this.data.serviceRecordInfo[0];
				} else {
					targetRecordObject = this.data.interfaceRecordInfo.find(info => info.id === mappingDetail.mappingRuleObject.recordId);

					if (!targetRecordObject) {
						targetRecordObject = this.data.interfaceRecordInfo[0];
					}
				}

				this.$refs.mappingRuleGrid.invoke('appendRow', {
					'pk.mappingId': mappingDetail.pk.mappingId,
					'pk.mappingOrder': mappingDetail.pk.mappingOrder,
					targetForm: mappingDetail.targetForm,
					mappingDetailDesc: mappingDetail.mappingDetailDesc,
					sourceRecordObject,
					targetRecordObject,
					ruleDetail: mappingDetail.mappingRuleObject.mappingRuleDetails.map(mappingRuleDetail => ({
						mappingExpression: mappingRuleDetail.mappingExpression,
						targetFieldPath: mappingRuleDetail.targetFieldPath,
						simpleMappingYn: mappingRuleDetail.simpleMappingYn
					}))
				});
			});

			if (0 === this.$refs.mappingRuleGrid.invoke('getRowCount')) return;

			this.mappingRuleGridSelectedRowKey = this.$refs.mappingRuleGrid.invoke('getRowAt', 0).rowKey;

			this.ruleClick(this.mappingRuleGridSelectedRowKey);
		}

		this.$nextTick(() => {
			this.resize();
		});
	},
	methods: {
		mappingArrowParentWheel(evt) {
			document.querySelectorAll(`#mappingInfo .tui-grid-rside-area .tui-grid-body-area`).forEach(element => {
				element.scrollTop += 40 * (0 < evt.deltaY ? 1 : -1);
			});
		},
		removeSingleMapping() {
			const dragZoneRowKey = this.$refs.sourceGrid.invoke('getFocusedCell').rowKey;

			if (!dragZoneRowKey) return;

			const id = this.$refs.sourceGrid.invoke('getValue', dragZoneRowKey, 'id');

			const ruleDetail = this.$refs.mappingRuleGrid.invoke('getValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail');

			this.$refs.mappingRuleGrid.invoke(
				'setValue',
				this.mappingRuleGridSelectedRowKey,
				'ruleDetail',
				ruleDetail.filter(info => info.mappingExpression !== id)
			);

			this.drawArrow();
		},
		mappingTitleBtnClick() {
			setTimeout(() => {
				this.mainResize();
			}, 300);
		},
		async getSameIdInfo() {
			if (null === this.mappingRuleGridSelectedRowKey) return;

			const mappingDetail = this.getMappingDetailObject();

			const info = this.$refs.mappingRuleGrid.invoke('getRow', this.mappingRuleGridSelectedRowKey);

			mappingDetail.mappingRuleObject.pk.mappingId = info[`pk.mappingId`];
			mappingDetail.mappingRuleObject.pk.mappingOrder = info[`pk.mappingOrder`];
			mappingDetail.mappingRuleObject.recordId = info['targetRecordObject']['id'];
			mappingDetail.mappingRuleObject.recordObject = info['targetRecordObject'];

			const recordInfo = `I` === this.data.type ? this.data.interfaceRecordInfo : this.data.serviceRecordInfo;

			info.sourceRecordObject.forEach((recordObject, idx) => {
				mappingDetail.mappingRuleObject[`mappingRuleSources`].push({
					pk: {
						mappingId: info[`pk.mappingId`],
						mappingOrder: Number(info[`pk.mappingOrder`]),
						parameterOrder: idx
					},
					recordId: recordObject['id'],
					recordObject: recordInfo.filter(info => info.id === recordObject['id'])[0]
				});
			});

			const result = await new HttpReq(`/api/entity/interface/generateMapping`).create(mappingDetail.mappingRuleObject);

			const mappingRuleDetails = result.data.object.mappingRuleDetails;

			this.$refs.mappingRuleGrid.invoke(
				'setValue',
				this.mappingRuleGridSelectedRowKey,
				'ruleDetail',
				mappingRuleDetails && 0 < mappingRuleDetails.length
					? mappingRuleDetails.map(mappingRuleDetail => ({
							mappingExpression: mappingRuleDetail.mappingExpression,
							targetFieldPath: mappingRuleDetail.targetFieldPath,
							simpleMappingYn: mappingRuleDetail.simpleMappingYn
					  }))
					: null
			);

			this.ruleClick(this.mappingRuleGridSelectedRowKey);
		},
		mainResize() {
			setTimeout(() => {
				this.resize();
			}, 250);
		},
		initTreeData(fields, parentId, depth = 1) {
			if (!fields) return;

			fields = cloneDeep(fields);

			const rtnArr = [];

			fields.forEach(field => {
				field['pk.fieldId'] = field.pk.fieldId;
				field['pk.recordId'] = field.pk.recordId;
				field['id'] = `${parentId}\\${field.pk.fieldId}`;

				if (field.options) {
					field.options.forEach(optionInfo => {
						field[optionInfo.pk.keyId] = optionInfo.property;
					});

					delete field.options;
				}

				if (!field._attributes) field._attributes = {};

				if (field.recordObject) {
					field.fieldType = 'R';

					field._attributes.expanded = true;

					if ('N' !== field.arrayType) field.id += `[i${depth}]`;

					field._children = this.initTreeData(field.recordObject.fields, field['id'], 'N' !== field.arrayType ? depth + 1 : depth);
				}

				delete field.pk;
				delete field.options;

				rtnArr.push(field);
			});

			return rtnArr;
		},
		getData() {
			this.$refs.mappingRuleGrid.invoke('blur');
			this.$refs.mappingRuleGrid.invoke('cancelEditing');

			const mappingDetails = [];

			this.$refs.mappingRuleGrid.invoke('getData').forEach(info => {
				const mappingDetail = this.getMappingDetailObject();

				mappingDetail.pk.mappingId = info[`pk.mappingId`];
				mappingDetail.pk.mappingOrder = Number(info[`pk.mappingOrder`]);
				mappingDetail.targetForm = info[`targetForm`];
				mappingDetail.mappingDetailDesc = info[`mappingDetailDesc`];

				mappingDetail.mappingRuleObject.pk.mappingId = info[`pk.mappingId`];
				mappingDetail.mappingRuleObject.pk.mappingOrder = info[`pk.mappingOrder`];
				mappingDetail.mappingRuleObject.recordId = info['targetRecordObject']['id'];

				info.sourceRecordObject.forEach((recordObject, idx) => {
					mappingDetail.mappingRuleObject[`mappingRuleSources`].push({
						pk: {
							mappingId: info[`pk.mappingId`],
							mappingOrder: Number(info[`pk.mappingOrder`]),
							parameterOrder: idx
						},
						recordId: recordObject['id']
					});
				});

				if (info.ruleDetail)
					info.ruleDetail.forEach((detail, idx) => {
						mappingDetail.mappingRuleObject[`mappingRuleDetails`].push({
							pk: {
								mappingId: info[`pk.mappingId`],
								mappingOrder: Number(info[`pk.mappingOrder`]),
								mappingDetailNo: idx
							},
							targetFieldPath: detail.targetFieldPath,
							mappingExpression: detail.mappingExpression,
							simpleMappingYn: 'Y',
							arraySizeParameter: null
						});
					});

				mappingDetails.push(mappingDetail);
			});

			return { mappingDetails };
		},
		ruleClick(rowKey) {
			if ('undefined' === typeof rowKey) return;

			this.mappingRuleGridSelectedRowKey = rowKey;

			const dataList = this.$refs.mappingRuleGrid.invoke('getData');

			dataList.forEach(data => {
				this.$refs.mappingRuleGrid.invoke('removeRowClassName', data.rowKey, 'row-selected');
			});

			this.$refs.mappingRuleGrid.invoke('addRowClassName', rowKey, 'row-selected');

			const { sourceRecordObject, targetRecordObject, ruleDetail } = this.$refs.mappingRuleGrid.invoke('getRow', rowKey);

			this.$refs.sourceGrid.invoke('clear');
			this.$refs.sourceGrid.invoke(
				'resetData',
				(() => {
					const sourceGridDataArr = [];

					sourceRecordObject.forEach((recordObject, idx) => {
						sourceGridDataArr.push({
							id: `$${idx}`,
							'pk.fieldId': recordObject.id,
							fieldType: 'R',
							_attributes: {
								expanded: true
							},
							_children: this.initTreeData(recordObject.fields, `$${idx}`)
						});
					});

					return sourceGridDataArr;
				})()
			);

			this.$refs.targetGrid.invoke('clear');
			this.$refs.targetGrid.invoke('resetData', [
				{
					id: ``,
					'pk.fieldId': targetRecordObject.id,
					fieldType: 'R',
					_attributes: {
						expanded: true
					},
					_children: this.initTreeData(targetRecordObject.fields, ``)
				}
			]);

			if (ruleDetail) {
				this.$nextTick(() => {
					const sourceGridData = this.$refs.sourceGrid.invoke('getData');
					const targetGridData = this.$refs.targetGrid.invoke('getData');

					for (let i = 0; i < ruleDetail.length; i++) {
						const info = ruleDetail[i];

						let isSourceValid = false;
						let isTargetValid = false;

						for (let i = 0; i < sourceGridData.length; i++) {
							if (sourceGridData[i].id === info.mappingExpression) {
								isSourceValid = true;
								break;
							}
						}

						for (let i = 0; i < targetGridData.length; i++) {
							if (targetGridData[i].id === info.targetFieldPath) {
								isTargetValid = true;
								break;
							}
						}

						if (!(isSourceValid && isTargetValid)) {
							this.$refs.mappingRuleGrid.invoke('setValue', rowKey, 'ruleDetail', null);
							this.$alert({ type: 'warn', message: this.$t('msg.mapping.relationship.broken') });
							break;
						}
					}
				});
			}
		},
		removeRule() {
			this.$refs.mappingRuleGrid.invoke('removeRow', this.mappingRuleGridSelectedRowKey);

			if (this.$refs.mappingRuleGrid.invoke('getRowCount')) {
				this.ruleClick(this.$refs.mappingRuleGrid.invoke('getRowAt', 0).rowKey);
			} else {
				const ctx = this.$refs.mappingArrow.getContext('2d');
				ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

				this.$refs.sourceGrid.invoke('clear');
				this.$refs.targetGrid.invoke('clear');

				this.mappingRuleGridSelectedRowKey = null;
			}
		},
		addRule() {
			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('label.modelRecord'),
				modalWidth: '600px',
				contentList: [
					{
						template: `<div><TuiGrid ref="addRuleGrid" v-bind="options" @click="onClick" /></div>`,
						components: {
							TuiGrid: Grid
						},
						data() {
							return {
								options: {
									header: {
										height: 32,
										align: 'center'
									},
									columnOptions: {
										resizable: true
									},
									scrollX: false,
									columns: [
										{
											name: 'recordObject',
											hidden: true
										},
										{
											header: this.$t('label.modelRecord.id'),
											name: 'id',
											escapeHTML: true
										},
										{
											header: this.$t('label.modelRecord.name'),
											name: 'name',
											escapeHTML: true
										},
										{
											header: this.$t('label.privilege.id'),
											name: 'privilegeId',
											escapeHTML: true
										}
									],
									data: []
								}
							};
						},
						created() {
							const recordInfo = `I` === _this.data.type ? _this.data.serviceRecordInfo : _this.data.interfaceRecordInfo;

							recordInfo.forEach(recordObject => {
								this.options.data.push({
									recordObject: recordObject,
									id: recordObject.id,
									name: recordObject.name,
									privilegeId: _this.data.privilegeId
								});
							});
						},
						methods: {
							onClick(evt) {
								if ('undefined' === typeof evt.rowKey) return;

								_this.$refs.mappingRuleGrid.invoke('appendRow', {
									sourceRecordObject: `I` === _this.data.type ? _this.data.interfaceRecordInfo : _this.data.serviceRecordInfo,
									targetRecordObject: evt.instance.getRow(evt.rowKey).recordObject,
									'pk.mappingId': _this.$parent.$refs.basicInfo.getData().mappingId,
									'pk.mappingOrder': _this.$refs.mappingRuleGrid.invoke('getData').length,
									targetForm: null,
									mappingDetailDesc: null
								});

								const mappingRuleGridDataList = _this.$refs.mappingRuleGrid.invoke('getData');

								const mappingRuleGridLastRowKey = mappingRuleGridDataList[mappingRuleGridDataList.length - 1].rowKey;

								_this.ruleClick(mappingRuleGridLastRowKey);

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		resize() {
			this.$nextTick(() => {
				const mappingModal = document.querySelector('#mappingModal');

				const width = mappingModal.clientWidth - 96;

				this.$refs.mappingRuleGridParent.style.width = width;
				this.$refs.mappingRuleGrid.invoke('setWidth', width);

				const sourceGridParentWidth = Math.floor(width * 0.35);
				const targetGridParentWidth = Math.floor(width * 0.35);
				const mappingArrowWidth = width - (sourceGridParentWidth + targetGridParentWidth) - 8;

				this.$refs.sourceGridParent.style.width = `${sourceGridParentWidth}px`;
				this.$refs.sourceGrid.invoke('setWidth', sourceGridParentWidth);

				this.$refs.targetGridParent.style.width = `${targetGridParentWidth}px`;
				this.$refs.targetGrid.invoke('setWidth', targetGridParentWidth);

				this.$refs.mappingArrowParent.style.width = `${mappingArrowWidth}px`;
				this.$refs.mappingArrow.setAttribute('width', mappingArrowWidth);

				const modalBodyHeight = document.querySelector(`#mappingModal`).parentNode.parentNode.clientHeight - 32;

				let minusHeight = 32 + 48 + 40 + 40 + 20 + document.querySelector(`#mappingModal #mappingRuleTitle`).offsetHeight;

				if (!this.data.readonly) minusHeight += 32;

				this.style.height = `${modalBodyHeight - minusHeight}px`;

				this.$refs.sourceGrid.invoke('setBodyHeight', modalBodyHeight - minusHeight - 40);
				this.$refs.targetGrid.invoke('setBodyHeight', modalBodyHeight - minusHeight - 40);
				this.$refs.mappingArrow.setAttribute('height', modalBodyHeight - minusHeight);

				this.$refs.sourceGridParent.style.height = `${modalBodyHeight - minusHeight}px`;
				this.$refs.targetGridParent.style.height = `${modalBodyHeight - minusHeight}px`;

				this.drawArrow();
			});
		},
		drawArrow() {
			if (!this.$refs.sourceGrid) return;

			if (!this.$refs.targetGrid) return;

			const sourceGridData = this.$refs.sourceGrid.invoke('getData');
			const targetGridData = this.$refs.targetGrid.invoke('getData');

			const ctx = this.$refs.mappingArrow.getContext('2d');

			ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

			const mappingRuleInfo = this.$refs.mappingRuleGrid.invoke('getRow', this.mappingRuleGridSelectedRowKey);

			if (null === mappingRuleInfo || null === mappingRuleInfo.ruleDetail) return;

			for (let i = 0; i < mappingRuleInfo.ruleDetail.length; i++) {
				const info = mappingRuleInfo.ruleDetail[i];

				let startYPos = 18,
					startY1Pos = 18;

				let sourceDataInfo = null,
					targetDataInfo = null;

				for (let i = 0; i < sourceGridData.length; i++) {
					let data = sourceGridData[i];

					if (data.id == info.mappingExpression) {
						sourceDataInfo = data;

						const groupInfoArr = [];

						for (let i = 0; i < sourceGridData.length; i++) {
							const sourceData = sourceGridData[i];

							if (`R` === sourceData.fieldType) {
								if (null === sourceData._attributes.tree.parentRowKey) {
									startYPos += 40;
								} else {
									if (!sourceData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
										groupInfoArr.splice(groupInfoArr.length - 1, 1);
									}

									if (groupInfoArr[groupInfoArr.length - 1].expanded) startYPos += 40;
								}

								groupInfoArr.push({ id: sourceData.id, expanded: sourceData._attributes.expanded });
							} else {
								if (!sourceData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
									groupInfoArr.splice(groupInfoArr.length - 1, 1);
								}

								if (groupInfoArr[groupInfoArr.length - 1].expanded) startYPos += 40;
							}

							if (data.id == sourceData.id) {
								break;
							}
						}

						break;
					}
				}

				for (let i = 0; i < targetGridData.length; i++) {
					let data = targetGridData[i];

					if (data.id == info.targetFieldPath) {
						targetDataInfo = data;

						const groupInfoArr = [];

						for (let i = 0; i < targetGridData.length; i++) {
							const targetData = targetGridData[i];

							if (`R` === targetData.fieldType) {
								if (null === targetData._attributes.tree.parentRowKey) {
									startY1Pos += 40;
								} else {
									if (!targetData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
										groupInfoArr.splice(groupInfoArr.length - 1, 1);
									}

									if (groupInfoArr[groupInfoArr.length - 1].expanded) startY1Pos += 40;
								}

								groupInfoArr.push({ id: targetData.id, expanded: targetData._attributes.expanded });
							} else {
								if (!targetData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
									groupInfoArr.splice(groupInfoArr.length - 1, 1);
								}

								if (groupInfoArr[groupInfoArr.length - 1].expanded) startY1Pos += 40;
							}

							if (data.id == targetData.id) {
								break;
							}
						}

						break;
					}
				}

				const drawStartYPos = startYPos - this.sourceScrollTop > 24 ? startYPos - this.sourceScrollTop : 24;
				const drawStartY1Pos = startY1Pos - this.targetScrollTop > 24 ? startY1Pos - this.targetScrollTop : 24;

				let isSelected = false;

				const sourceGridCell = this.$refs.sourceGrid.invoke(`getFocusedCell`);

				if (null !== sourceGridCell.rowKey) {
					const { id } = this.$refs.sourceGrid.invoke(`getRow`, sourceGridCell.rowKey);
					isSelected = id === info.mappingExpression;
				}

				if (!isSelected) {
					const targetGridCell = this.$refs.targetGrid.invoke(`getFocusedCell`);

					if (null !== targetGridCell.rowKey) {
						const { id } = this.$refs.targetGrid.invoke(`getRow`, targetGridCell.rowKey);

						isSelected = id === info.targetFieldPath;
					}
				}

				let isValidation = true;

				if (
					sourceDataInfo.fieldType !== targetDataInfo.fieldType ||
					(0 !== sourceDataInfo.fieldLength &&
						0 !== targetDataInfo.fieldLength &&
						sourceDataInfo.fieldLength !== targetDataInfo.fieldLength)
				) {
					isValidation = false;
				}

				this.drawLineBoth(ctx, drawStartYPos, drawStartY1Pos, isSelected, isValidation);
			}
		},
		drawLineBoth(ctx, startYPos, startY1Pos, isSelected, isValidation) {
			let lineColor = null;

			if (isValidation) lineColor = isSelected ? '#212F39' : `#90969B`;
			else lineColor = isSelected ? 'rgba(237, 49, 49, 1)' : `rgba(237, 49, 49, 0.8)`;

			ctx.save();

			if (isSelected) ctx.lineWidth = 5;

			ctx.beginPath();

			ctx.moveTo(0, startYPos);
			ctx.lineTo(50, startYPos);
			ctx.lineTo(ctx.canvas.width - 50, startY1Pos);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.strokeStyle = lineColor;
			ctx.stroke();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(0, startYPos - 5);
			ctx.lineTo(0, startYPos + 5);
			ctx.lineTo(10, startYPos + 5);
			ctx.lineTo(10, startYPos - 5);
			ctx.lineTo(0, startYPos - 5);
			ctx.fillStyle = lineColor;
			ctx.fill();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(ctx.canvas.width, startY1Pos);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos - 8);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos + 8);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.fillStyle = lineColor;
			ctx.fill();
			ctx.closePath();

			ctx.restore();
		},
		drawLineTarget(ctx, startYPos, startY1Pos) {
			ctx.beginPath();
			ctx.fillStyle = 'gray';
			ctx.fillText('|F|', ctx.canvas.width - ctx.canvas.width / 3 - 14, startY1Pos + 3);
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(ctx.canvas.width - ctx.canvas.width / 3, startY1Pos);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.save();
			ctx.setLineDash([5, 5]);
			ctx.strokeStyle = 'gray';
			ctx.stroke();
			ctx.restore();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(ctx.canvas.width, startY1Pos);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos - 8);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos + 8);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.fillStyle = 'gray';
			ctx.fill();
			ctx.closePath();
		},
		initDragDropEvt() {
			if ($(this.$refs.sourceGridParent).draggable()) {
				$(this.$refs.sourceGridParent).draggable('destroy');
			}

			if ($(this.$refs.targetGridParent).find('td').droppable()) {
				$(this.$refs.targetGridParent).find('td').droppable('destroy');
			}

			if (this.data.readonly) return;

			$(this.$refs.sourceGridParent).draggable({
				revert: true,
				revertDuration: 0,
				helper: () => `<span></span>`,
				start: () => {
					const dragZoneRowKey = this.$refs.sourceGrid.invoke('getFocusedCell').rowKey;

					if (!dragZoneRowKey) return false;

					if (0 < this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey)._children.length) return false;

					this.dragger = document.createElement('div');
					this.dragger.classList.add('dragger');
					this.dragger.innerText = this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey).fieldName;

					document.body.appendChild(this.dragger);

					document.body.style.cursor = `move`;

					const dataList = this.$refs.sourceGrid.invoke('getData');

					dataList.forEach(data => {
						this.$refs.sourceGrid.invoke('removeRowClassName', data.rowKey, 'row-selected');
					});

					this.$refs.sourceGrid.invoke('addRowClassName', dragZoneRowKey, 'row-selected');
				},
				drag: evt => {
					const dragZoneRowKey = this.$refs.sourceGrid.invoke('getFocusedCell').rowKey;

					if (0 < this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey)._children.length) return false;

					this.dragger.style.top = `${evt.pageY}px`;
					this.dragger.style.left = `${evt.pageX + 15}px`;
				},
				stop: () => {
					document.body.removeChild(this.dragger);
					document.body.style.cursor = `auto`;
				}
			});

			$(this.$refs.targetGridParent).droppable({
				tolerance: 'pointer',
				drop: evt => {
					const dragZoneRowKey = this.$refs.sourceGrid.invoke('getFocusedCell').rowKey;

					let targetElement = evt.originalEvent.target;
					let dropZoneRowKey = null;

					while (null == dropZoneRowKey && targetElement !== evt.target) {
						if (targetElement.getAttribute('data-row-key')) {
							dropZoneRowKey = Number(targetElement.getAttribute('data-row-key'));
						} else {
							targetElement = targetElement.parentElement;
						}
					}

					if (null === dropZoneRowKey) return;

					const ruleDetail = this.$refs.mappingRuleGrid.invoke('getValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail');

					if (
						ruleDetail &&
						ruleDetail.find(rule => rule.targetFieldPath === this.$refs.targetGrid.invoke('getValue', dropZoneRowKey, 'id'))
					) {
						if (
							ruleDetail.find(
								rule =>
									rule.mappingExpression === this.$refs.sourceGrid.invoke('getValue', dragZoneRowKey, 'id') &&
									rule.targetFieldPath === this.$refs.targetGrid.invoke('getValue', dropZoneRowKey, 'id')
							)
						) {
							return false;
						}

						this.$confirm({
							type: 'warn',
							message: this.$t(`msg.mapping.override`),
							callback: () => {
								this.$refs.mappingRuleGrid.invoke(
									'setValue',
									this.mappingRuleGridSelectedRowKey,
									'ruleDetail',
									ruleDetail
										.filter(rule => rule.targetFieldPath !== this.$refs.targetGrid.invoke('getValue', dropZoneRowKey, 'id'))
										.map(info => ({
											targetFieldPath: info.targetFieldPath,
											mappingExpression: info.mappingExpression,
											simpleMappingYn: info.simpleMappingYn
										}))
								);

								makeRuleDetail.call(this);
							}
						});
					} else {
						makeRuleDetail.call(this);
					}

					function makeRuleDetail() {
						if (0 < this.$refs.targetGrid.invoke('getRow', dropZoneRowKey)._children.length) return false;

						const targetFieldPath = this.$refs.targetGrid.invoke('getRow', dropZoneRowKey).id;
						const mappingExpression = this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey).id;
						const mappingRuleInfo = this.$refs.mappingRuleGrid.invoke('getRow', this.mappingRuleGridSelectedRowKey);

						let newRuleDetail = null;

						if (null === mappingRuleInfo.ruleDetail) {
							newRuleDetail = [
								{
									targetFieldPath,
									mappingExpression,
									simpleMappingYn: 'Y'
								}
							];
						} else {
							mappingRuleInfo.ruleDetail.push({
								targetFieldPath,
								mappingExpression,
								simpleMappingYn: 'Y'
							});

							newRuleDetail = mappingRuleInfo.ruleDetail.map(info => ({
								targetFieldPath: info.targetFieldPath,
								mappingExpression: info.mappingExpression,
								simpleMappingYn: info.simpleMappingYn
							}));
						}

						this.$refs.mappingRuleGrid.invoke('setValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail', newRuleDetail);

						this.resize();
					}
				}
			});
		},
		getMappingDetailObject() {
			return {
				pk: {
					mappingId: null,
					mappingOrder: null
				},
				targetForm: null,
				mappingDetailDesc: null,
				mappingRuleObject: {
					pk: {
						mappingId: null,
						mappingOrder: null
					},
					recordId: null,
					mappingRuleSources: [],
					mappingRuleDetails: []
				}
			};
		},
		initMappingInfo() {
			this.$refs.mappingRuleGrid.invoke('setValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail', null);
			this.drawArrow();
		}
	},
	destroyed() {
		WindowResizeEventBus.$off('MainResize', this.mainResize);
		SideMenuToggleEventBus.$off('MainResize', this.mainResize);
	}
};
</script>

<style lang="scss">
.dragger {
	z-index: 9999;
	position: absolute;
	min-width: 80px;
	min-height: 35px;
	line-height: 35px;
	text-align: center;
	background-color: #484f70;
	color: #fff;
	border-radius: 4px;
}

#mappingInfo {
	.table-responsive {
		display: inline-block;

		.tui-grid-layer-selection {
			display: none;
		}

		.tui-grid-cell-current-row {
			td {
				background: #f5f6fb;
			}
		}
	}
}
</style>
